import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { create } from 'domain';
import { ListarUnidadeMedidaComponent } from './listar/listar-unidade-medida.component';


const routes: Routes = [
  {
    path: '',
    redirectTo: 'listar',
    pathMatch: 'full',
  },
  {
    path: '',
    data: {
      title: 'Unidade medida'
    },
    children: [
      {
        path: 'listar',
        component: ListarUnidadeMedidaComponent,
        data: {
          title: 'Listar'
        }
      },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class UnidadeMedidaRoutingModule { }



