import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Component, Output, ViewChild, OnInit, EventEmitter } from '@angular/core';

import { AbstractPopupComponent } from 'config/core/abstract-popup.component';
import { UnidadeMedidaService } from './../unidade-medida.service';
import { UnidadeMedidaDTO } from './../../../../../models/domain/unidade-medida.dto';


@Component({
  selector: 'unidade-popup-modal',
  templateUrl: './unidade-medida-popup-modal.component.html',
  styleUrls: ['./unidade-medida-popup-modal.component.scss']

})
export class UnidadeMedidaPopupModalComponent extends AbstractPopupComponent implements OnInit {

  constructor(
    public unidadeService: UnidadeMedidaService,
    public fb: FormBuilder) {
    super();
  }

  ngOnInit() {
    this.criarForm();
  }

  show() {
    this.criarForm();
    this.showPopup("Cadastra unidade de medida")
  }

  showAlteracao(item: UnidadeMedidaDTO) {
    this.alterarForm(item);
    this.showPopup("Alterar unidade de medida")
  }

  btnConfirmar(unidade: UnidadeMedidaDTO) {
    this.acao(unidade, this.unidadeService);
  }

  criarForm() {
    this.formulario = this.fb.group({
      id: [''],
      nome: ['', Validators.required],
      sigla: ['', Validators.required],
      emAlteracao: [false]
    })
  }

  alterarForm(item: UnidadeMedidaDTO) {
    this.formulario = this.fb.group({
      id: [item.id],
      nome: [item.nome, Validators.required],
      sigla: [item.sigla, Validators.required],
      emAlteracao: [true]
    })
  }

}



