import { TrataErrorService } from 'services/trata-error.service';
import { AbstractBaseComponent } from 'config/core/abstract-base.component';
import { FieldMessage } from './../../models/config/field-message';
import { ElementRef, ViewChild, Output, EventEmitter, Input } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';

import { SelectItem } from 'ng2-select';
import { ComboDTO } from 'models/domain/combo.dto';
import { EventEmitterService } from 'app/shared/event/event-emitter.services';


export abstract class AbstractPopupComponent extends AbstractBaseComponent {

    @ViewChild('campoNome') campo;
    @Output() onClickConfirm = new EventEmitter<any>();

    protected titulo: string = "";

    constructor() {
        super()
    }

    ngOnInit() {
    }

    protected tratarSucesso() {
        EventEmitterService.getClosePopup().emit(true);
        this.onClickConfirm.emit();
    }

    protected showPopup(titulo: string) {
        EventEmitterService.getError422().emit(null);
        EventEmitterService.getShowPopup().emit();
        this.titulo = titulo;
        this.fieldFocus(this.campo);
    }


    protected acao(item: any, service: any) {
        if (item.emAlteracao) {
            service.atualizar(item)
                .subscribe(
                    res => this.tratarSucesso(),
                    error => TrataErrorService.tratarError(error));
        } else {
            service.salvar(item)
                .subscribe(
                    res => this.tratarSucesso(),
                    error => TrataErrorService.tratarError(error));
        }
    }

}



