import { UnidadeMedidaPopupModalComponent } from './popup/unidade-medida-popup-modal.component';

import { NgModule } from '@angular/core';
import { HttpClientModule } from '@angular/common/http';
import { PopupModalModule } from 'app/shared/popup/popup-modal.module';
import { ModalModule } from 'ng2-bootstrap/modal';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';

import { SharedComponentModule } from './../../../shared/shared-component.module';
import { UnidadeMedidaService } from './../unidademedida/unidade-medida.service';
import { ListarUnidadeMedidaComponent } from './../unidademedida/listar/listar-unidade-medida.component';
import { UnidadeMedidaRoutingModule } from './../unidademedida/unidade-medida-routing.module';


@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ModalModule.forRoot(),
    SharedComponentModule,
    ReactiveFormsModule,
    PopupModalModule,
    HttpClientModule,
    UnidadeMedidaRoutingModule
  ],
  declarations: [
    ListarUnidadeMedidaComponent,
    UnidadeMedidaPopupModalComponent
  ],
  providers:[
    UnidadeMedidaService
  ]
})
export class UnidadeMedidaModule { }



