import { API_CONFIG } from 'config/core/api.config';
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Observable } from "rxjs/Rx";
import { Injectable } from '@angular/core';

import { AbstractService } from 'config/core/abstract-service';
import { UnidadeMedidaDTO } from './../../../../models/domain/unidade-medida.dto';

@Injectable()
export class UnidadeMedidaService extends AbstractService {

    url:string = `${API_CONFIG.baseUrl}/unidademedida`;

    constructor(public http: HttpClient) {
        super();
    }

    findAll(): Observable<UnidadeMedidaDTO[]> {
        return this.http.get<UnidadeMedidaDTO[]>(`${this.url}/all`, this.getHearderToken());
    }
    findAllAtivo(): Observable<UnidadeMedidaDTO[]> {
        return this.http.get<UnidadeMedidaDTO[]>(`${this.url}/`);
    }

    findById(id: string): Observable<{}> {
        return this.http.get(`${this.url}/${id}`, this.getHearderToken());
    }

    salvar(unidade: UnidadeMedidaDTO) {
        return this.http.post(`${this.url}/`, JSON.stringify(unidade), this.getHearderTokenNoResponse());
    }

    changeStatus(unidade: UnidadeMedidaDTO) {
        return this.http.put(`${this.url}/${unidade.id}/changeStatus`, {}, this.getHearderTokenNoResponse());
    }

    atualizar(unidade: UnidadeMedidaDTO) {
        return this.http.put(`${this.url}/${unidade.id}`, JSON.stringify(unidade), this.getHearderTokenNoResponse());
    }

    excluir(unidade: UnidadeMedidaDTO): Observable<{}> {
        let url = `${this.url}/${unidade.id}`;
        return this.http.delete(url, this.getHearderToken());
    }
}



